import React, {useState,useEffect} from 'react'
import './Projects.scss'
import {motion} from 'framer-motion'
import { urlFor,client } from '../../clients'
const Projects = () => {
  const [activeFilter, setActiveFilter] = useState('ALL')
  const [animateCard, setAnimateCard] = useState({y:0,opacity:1})
  const [projects, setProjects] = useState([])
  const [filterProject, setFilterProject] = useState([])
  useEffect(()=> {
    const query = '*[_type == "projects"]';
    client.fetch(query).then((data) => {setProjects(data);setFilterProject(data);})
  },[])
  const handleProjectFilter = (item) => {
    setActiveFilter(item)
    setAnimateCard([{y:0,opacity:0}])
    setTimeout(()=>{
      setAnimateCard([{y:0,opacity:1}])
      if(item === 'ALL'){
        setFilterProject(projects);
      }else{
        setFilterProject(projects.filter((project) => project.tag.includes(item)))
      }
    },500)
  };

  return (
    <>
    <div className="app__project-filter">
      {['ALL','UI/UX', 'Web Apps','MERN'].map((item,index)=>(
        <div key={index} onClick={()=> handleProjectFilter(item)} className={`app__project-filter-item app__flex p-text ${activeFilter === item ? 'item-active' : ''} `}>
          {item}
        </div>
      ))}
    </div>

    <motion.div animate={animateCard} transition={{duration:.1, delayChildren:.1}} className="app__project-portfolio">
        {filterProject.map((filterproject,index) => (
          
          <div className='app__project-item app__flex' key={index}>
            <div className="app__project-item-header">
              <div className="app__project-item-header-logo app__flex">
              <img src={urlFor(filterproject.logo)} alt={filterproject.name} />
              </div>
              <div className="app__project-item-header-name">
                <p>{filterproject.name}</p>
              </div>
            </div>
            <div className='app__project-item-img app__flex'>
              <a href={filterproject.link} rel="alternate" target="_blank" type="text/html"><img src={urlFor(filterproject.imgUrl)} alt={filterproject.name}/></a>
            </div>
            <div className="app__project-item-footer">
              <h3>{filterproject.title}</h3>
              <div className="app__project-item-footer-tags">
                {filterproject.tag.map((tagname,index)=> (
                  <span key={tagname+index} >{tagname}</span>
                ))}
              </div>
            </div>
          </div>
        ))}
    </motion.div>
    </>
  )
}

export default Projects