import React from 'react'
import './Footer.scss'
import Icons from '../../components/socialicons/socialIcons'
import ContactFrom from '../../components/Contactform/ContactFrom'
const Footer = () => {
  return (
    <div className='app__footer'>
    <div className="app__footer-links">
      <h2>If you’re looking for a freelance designer to help bring an idea to life and you’re on a tight timeline—let’s jam.</h2>
    <Icons/>
    </div>
    <div className="app__footer-contactform">
      <ContactFrom/>
    </div>
    </div>
  )
}

export default Footer