import React , {useState,useEffect} from 'react'

import './Navbar.scss'
import { images } from '../../constants'
import {HiMenuAlt4,HiX} from 'react-icons/hi'
import {motion} from 'framer-motion'
import { urlFor,client } from '../../clients'

import  Icons from '../socialicons/socialIcons';
const Navbar = () => {
    const [details, setDetails] = useState([]);
    useEffect(() => {
        const query = '*[_type == "otherdetails"]';
        client.fetch(query).then((data) => {
            setDetails(data)
        })
    },[]);
    const [toggle, setToggle] = useState(false)
  return (
    <nav className='app__navbar'>
        {details.length === 0 ? "waiting" :
        <div className='app__navbar-logo'>
            <img src={urlFor(details[0].logo)} alt="my image" />
            <span className='app__navbar-name'>{details[0].name}</span>
        </div>}
        <ul className='app__navbar-links'>
            <Icons/>
        </ul>
        <div className="app__navbar-menu">
                <HiMenuAlt4 onClick={()=> setToggle(true)}/>
                {toggle && (
                    <motion.div
                        whileInView={{x:[300,0]}}
                        transition={{duration:0.85,ease:'easeInOut'}}
                    >
                        <HiX onClick={()=> setToggle(false)}/>
                        <ul className='app__navbar-links'>
                        {[images.github,images.linkedin,images.fiverr,images.instagram].map((item)=>(
                    <li key={item}>
                    <a href={item} rel="alternate" target="_blank" type="text/html" onClick={()=> setToggle(false)}><img src={item} alt="my image" /></a>
                </li>
            ))}</ul>
                    </motion.div>
                )}
        </div>
    </nav>
  )
}

export default Navbar