import React , {useState,useEffect,useRef} from 'react'
import Typed from 'typed.js';
import {motion} from 'framer-motion'
import './Header.scss'
import {client } from '../../clients'

const Header = () => {
  const [header, setHeader] = useState(["ram"]);
  useEffect(() => {
    const query = '*[_type == "otherdetails"]';
    client.fetch(query).then((data) => {
      setHeader(data)
    })
  });
  let el = useRef();
  useEffect(() => {
    const typed = new Typed('.changetext' , {
      strings:['Companies','Startups','Businesses','Individuals'],
      startDelay:0,
      typeSpeed:150,
      backDelay:3000,
      backSpeed:150,
      smartBackspace:true,
      showCursor:false,
      loop:true
      });
    },[]);
  
  return (
    <div className="app__header app__flex">
      <motion.div whileInView={{x:[-100,0],opacity:[0,1]}}
      transition={{duration:0.5}}
      className="app__header-info"
      >
          <div className='app__header-info-wrapper'>
          <h1>{header[0].headline} <span className="changetext" ref={el}></span></h1>
          <h3>{header[0].subheadline}</h3>
          </div>
      </motion.div>
    </div>
  )
}

export default Header