import React, {useState,useEffect} from 'react'
import { urlFor,client } from '../../clients'
import './socialIcons.scss'

const Icons = () => {
    const [icons, setIcons] = useState([])
    useEffect(()=> {
        const query = '*[_type == "social"]';
        client.fetch(query).then((data) => {setIcons(data)})
      },[]);

  return (
    <>
     <ul className='app__social-links'>
            {icons.map((item)=>(
                <li className='app__flex p-text' key={`${item.name}`}>
                    <a href={item.link} rel="alternate" target="_blank" type="text/html"><img src={urlFor(item.logo)} alt={`${item.name}`}/></a>
                </li>
            ))}
        </ul>
    </>
  )
}

export default Icons