import React from 'react'
import { Projects,Footer,Header } from './container';
import { Navbar } from './components';
import './App.scss'

const App = () => {
  return (
    <div className="app">
        <div className="background"></div>
        <Navbar/>
        <Header/>
        <Projects/>
        <Footer/>
    </div>
  );
}

export default App;